	module.exports = {"data":{"markdownRemark":{"html":"<p>到劳动人民文化宫去，上海和广东将在这里组织国庆游园活动。</p>\n<p>这里汇集了上海的文化，就是上海的设计思想，让来的人做一天上海人。有南京路的模型，有淮海路的风景，有有名的大企业，如上海一桑塔纳汽车公司、贝尔电话公司等等。各式各样的名店也在这里开一家小门面，上海的小吃也来了，在河边一线排开。整体设计思想比较先进，反映一种现代艺术的构思。</p>\n<p>现代气氛比较浓厚。所有的景物，不如这种设计思想重要。这种设计思想最能反映上海近年来的变化，追求现代性。</p>\n<p>追求现代性，在现代社会中越来越表现在人们的思想观念中，而不在物质的层面。在物质不构成现代性的困难的时候，观念就显得异常重要。而在物质没有发展到这一步的时候，观念也无法脱颖而出。物质均具备，最后的竞争就是观念，就是把自然物质变为人化自然的能力。</p>\n<p>在这方面，我们开始突破，东方明珠、歌剧院、音乐厅、博物馆均是渗透着现代观念的物质载体，与一段时间装修的商业街相比，现代性要强得多，是观念上新的飞跃。当然，目前这种飞跃体现在为数不多的方面，大部分物质存在还没有突破，还在物质本身的现代性，如装饰材料质地、颜色等。什么时候所有小的方面均超越了物质外壳，有观念性的东西蕴藏在里面，社会就大大进步。</p>\n<p>衡量标准，倒不是大型物体，而是微型物体。现代性的观念总是要从大型到小型，因为只有大型市场被占领之后，小型才能轮到。现代观念的竞争发展到小型竞争时，社会的现代化程度就密集了。</p>","id":"/home/swain/workspace/political-life/src/pages/zh/0148.md absPath of file >>> MarkdownRemark","fileAbsolutePath":"/home/swain/workspace/political-life/src/pages/zh/0148.md","frontmatter":{"title":"9月25日 星期日"}}},"pathContext":{"id":"/home/swain/workspace/political-life/src/pages/zh/0148.md absPath of file >>> MarkdownRemark"}}

